.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.btn {
  position: relative;
  display: inline-block;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: #333;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-color: #eee;
  background-image: linear-gradient(#fcfcfc, #eee);
  border: 1px solid #d5d5d5;
  border-radius: 3px;
  appearance: none;
  text-align: center;
  text-decoration: none;
}

.btn:hover {
  background-color: #ddd;
  background-image: linear-gradient(#eee, #ddd);
  border-color: #ccc;
  color: #333;
  text-decoration: none;
}

.btn-primary {
  background-color: #60b044;
  background-image: linear-gradient(#8add6d, #60b044);
  border-color: #5ca941;
  color: #fff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.15);
}

.btn-primary:hover {
  background-color: #569e3d;
  background-image: linear-gradient(#79d858, #569e3d);
  border-color: #4a993e;
  color: #fff;
}

.btn-danger {
  color: #900;
}

.btn-danger:hover {
  background-color: #b33630;
  background-image: linear-gradient(#dc5f59, #b33630);
  border-color: #cd504a;
  color: #fff;
}

.btn-add {
  position: relative;
  top: 3px;
  border: 1px solid #576675;
  border-radius: 50%;
  width: 15px;
  margin-right: 3px;
}

header {
  background-color: #325776;
}

header .main-cta {
  position: relative;
  top: 16px;
}

.logo {
  margin: 10px 15px;
}

.logo a:hover {
  background-color: transparent;
  color: #fff;
}

.header-search {
  padding: 0;
  margin: auto 0;
}

.header-search-form {
  position: relative;
}

.header-search-form span {
  color: #d7d7d7;
  font-size: 12px;
  left: 10px;
  position: absolute;
  top: 10px;
}

.header-search-form input {
  border: 0;
  color: #fff;
  padding-left: 29px;
}

.header-nav {
  margin-top: 24px;
  text-align: right;
}

.header-nav-item {
  display: inline;
  margin-right: 10px;
}

.header-nav-item:last-child {
  margin-right: 0;
}

.header-nav-item-link {
  color: #fff;
  font-weight: 300;
  padding: 3px 11px;
  text-decoration: none;
}

.header-nav-item-link:hover {
  background-color: #497193;
  border-radius: 3px;
  color: #fff;
  padding: 3px 11px;
  text-decoration: none;
}

.header-search-input {
  background: #497193;
  border-color: #497193;
  color: white;
}

.header-search-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.header-search-input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.header-search-input:focus {
  background: white;
  color: #323232;
}

.header-search-results {
  position: absolute;
  width: 100%;
  z-index: 10;
}

.header-search-result {
  position: relative;
  background: white;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid #eee;
}

.header-search-result a {
  color: inherit;
  text-decoration: none;
  vertical-align: middle;
  background: transparent;
}

.header-search-result a span {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.header-search-result a:hover {
  background: inherit;
  color: inherit;
}

.header-search-result .avatar {
  border-radius: 3px;
  display: inline-block;
  height: 36px;
  margin: 10px;
  width: 36px;
}

.header-search-result .avatar-initials {
  text-align: center;
  padding-top: 6px;
  font-size: 15px;
  color: #fff;
}

.header-search-result:last-child {
  border-bottom: initial;
}

.header-search-result:hover {
  background: #f5f5f5;
}

@media (max-width: 767px) {
  header .mobile-menu {
    background-color: #58748c;
    border: 1px solid #325776;
    margin-bottom: 20px;
  }
  header .mobile-menu li {
    border-bottom: 1px solid #475b6b;
    margin-bottom: 0;
    padding: 4px 0;
  }
  header .mobile-menu li a {
    text-decoration: none;
  }
  header .mobile-menu li:last-child {
    border-bottom: 0;
  }
  header .mobile-menu li.cta {
    border: 0;
  }
  header .mobile-menu li.cta a {
    width: 100%;
  }
  .header-search {
    padding: 0;
    margin: 20px 0;
  }
  .header-search ul {
    padding-right: 26px;
  }
}

.dashboard {
  background-color: #fff;
  padding-top: 50px;
}

.dashboard .tab-content {
  border-left: 1px solid #dfdfdf;
  border-right: 1px solid #dfdfdf;
  border-bottom: 1px solid #dfdfdf;
  border-radius: 0 0 3px 3px;
  padding: 15px;
}

.dashboard .nav-tabs a {
  text-decoration: none;
}

.dashboard .tab-content li {
  padding: 5px 0;
}

.dashboard .tab-content li:not(:last-child) {
  border-bottom: 1px solid #dfdfdf;
}

.dashboard .reminders .reminder-in-days {
  font-style: italic;
  font-size: 12px;
}

.dashboard .dashboard-box {
  background-color: #fff;
  border: 1px solid #dfdfdf;
  border-radius: 3px;
  padding: 15px;
}

.dashboard .dashboard-box h2 {
  border-bottom: 1px solid #eee;
  font-size: 14px;
  font-weight: 600;
  padding-bottom: 5px;
}

.dashboard .dashboard-box.dashboard-stat {
  margin-bottom: 40px;
  text-align: center;
}

.dashboard .dashboard-box.dashboard-stat li {
  display: inline-block;
}

.dashboard .dashboard-box.dashboard-stat li:not(:last-child) {
  margin-right: 25px;
}

.dashboard .dashboard-box.dashboard-stat .stat-number {
  display: block;
  font-size: 25px;
}

.dashboard .dashboard-box.dashboard-stat .stat-description {
  font-size: 13px;
}

.dashboard .section-icon {
  width: 19px;
  position: relative;
  top: -3px;
}

.dashboard h3 {
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  padding-left: 5px;
  margin-bottom: 10px;
}

.dashboard .dashboard-section {
  position: relative;
}

.dashboard .dashboard-section:not(:first-child) {
  margin-top: 20px;
}

.dashboard .dashboard-section .dashboard-item {
  display: table;
  table-layout: fixed;
  width: 100%;
  padding: 5px 0;
}

.dashboard .dashboard-section .dashboard-item:not(:last-child) {
  border-bottom: 1px solid #dfdfdf;
}

.dashboard .dashboard-section .truncate {
  display: table-cell;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.dashboard .sidebar-cta a {
  width: 100%;
}

.dashboard .sidebar-box {
  margin-top: 20px;
}

.dashboard .sidebar-box h3 {
  font-size: 15px;
  font-weight: 400;
  margin-bottom: 10px;
}

.dashboard .sidebar-box a {
  font-size: 12px;
}

.dashboard .last-seen .avatar {
  background-color: #93521e;
  border-radius: 3px;
  color: #fff;
  display: inline-block;
  height: 30px;
  margin-bottom: 5px;
  margin-right: 5px;
  padding-left: 5px;
  padding-top: 4px;
  position: relative;
  top: -3px;
  width: 30px;
}

.dashboard .last-seen .avatar.one-letter {
  padding-left: 0;
  text-align: center;
}

.dashboard .last-seen .avatar a {
  color: #fff;
  font-size: 14px;
  text-decoration: none;
}

.dashboard .event-list-item {
  color: #717171;
  margin-bottom: 5px;
  padding: 10px;
  position: relative;
  top: 2px;
}

.dashboard .event-list-item .event-icon {
  display: inline;
}

.dashboard .event-list-item .event-icon img {
  position: absolute;
  top: 5px;
  width: 19px;
}

.dashboard .event-list-item .event-date {
  color: #b1b1b1;
  display: inline;
  font-size: 11px;
  margin-right: 10px;
}

